/**
 * @file
 * Subtheme specific CSS.
 */
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap");
body {
  font-size: 16px;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6,
strong {
  font-weight: 700;
}

.navbar-top--wrapper {
  padding: 20px 0;
  background: #333333;
}
.navbar-top--wrapper a {
  color: #eeeeee;
  text-decoration: none;
}
.navbar-top--wrapper a:hover {
  text-decoration: underline;
  color: #ffffff;
}
.navbar-main--wrapper {
  padding: 20px 0;
  background: #cccccc;
}
.navbar-main--wrapper .navbar-expand-lg ul.navbar-nav li a {
  background: #222222;
  color: #cccccc;
  border-radius: 15px;
  padding: 10px 20px;
  border: 2px solid #318abd;
  margin-left: 10px;
}
.navbar-main--wrapper .navbar-expand-lg ul.navbar-nav li a:hover {
  background: #000000;
  color: #ffffff;
}
.navbar-main--wrapper .navbar-expand-lg ul.navbar-nav li ul li a {
  background: red;
}

.navbar-expand-lg .navbar-collapse {
  flex-grow: 0;
}

footer.site-footer {
  background: #0e0b0b;
  padding: 0;
  margin-top: 30px;
}
footer.site-footer ul.nav {
  flex-direction: column !important;
}
footer.site-footer ul.nav li a {
  display: block;
  padding: 0;
  margin-bottom: 15px;
  color: #ffffff;
}
footer.site-footer ul.nav li a:hover {
  text-decoration: underline;
}
footer.site-footer .footer-top {
  background: #878787;
  color: #333333;
  padding: 20px 0;
  margin-bottom: 20px;
}
footer.site-footer .footer-bottom {
  background: #000000;
  color: #ffffff;
  padding: 20px 0;
  margin-top: 20px;
}
footer.site-footer .footer-bottom a {
  color: #ffffff;
  text-decoration: underline;
}
footer.site-footer .footer-bottom a:hover {
  text-decoration: none;
}
footer.site-footer .site-footer__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.header-alert--wrapper {
  background: #8d0e0e;
  padding: 20px 0;
}
.header-alert--wrapper .container {
  color: #ffffff;
}
.header-alert--wrapper .container strong {
  font-size: 32px;
}
.header-alert--wrapper .container a {
  color: #ffffff;
}
.header-alert--wrapper .container a:hover {
  text-decoration: none;
  color: black;
}

.node--type-recipes .recipes-top .field--type-image {
  float: none;
}
.node--type-recipes .recipes-top .field--type-image img, .node--type-recipes .recipes-top .field--type-image picture {
  width: 100%;
}
.node--type-recipes .recipes-main .recipes-time {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  background: #eeeeee;
  padding: 15px;
  margin: 15px 0;
}
.node--type-recipes .recipes-main .recipes-time .field__label {
  font-weight: bold;
}
.node--type-recipes .recipes-main .col-md-3 .field__label {
  font-weight: bold;
}
.node--type-recipes .recipes-main .col-md-3 .field {
  margin-bottom: 30px;
  background: #eeeeee;
  padding: 15px;
  border-radius: 15px;
}

.recipes-card .recipe-body {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
}